import React, { Component } from 'react'
import { Link } from 'react-router-dom'

import './sass/Card.scss'

import CompletedIndication from './CompletedIndication.js'
import { CommentOutlined, CheckOutlined } from '@ant-design/icons'

class Card extends Component {
  authors () {
    const { authors } = this.props
    if (!authors) {
      return null
    }

    return (
      <tr>
        <td></td>
        <td>{authors.sort().join()}</td>
      </tr>
    )
  }

  genres () {
    const { genres } = this.props
    if (!genres) {
      return null
    }

    return (
      <tr>
        <td></td>
        <td>{genres.sort().join(', ')}</td>
      </tr>
    )
  }

  foreignLanguages () {
    const getList = (data, lang, title) => {
      if (!data) {
        return null
      }

      const url = `https://raw.githubusercontent.com/high-otaku/assets/master/flags/${lang}.png`
      return (
        <div className='icon-container'>
          <img className='flag' src={url} title={title} alt={title} />
          {data.sort().join(', ')}
        </div>
      )
    }

    const { meta } = this.props
    if (!meta) {
      return null
    }

    return (
      <tr>
        <td> </td>
        <td>
          {getList(meta.jp, 'jp', '')}
          {getList(meta.en, 'en', 'english')}
          {getList(meta.ru, 'ru', '')}
        </td>
      </tr>
    )
  }

  rating () {
    const { rating } = this.props
    if (!rating) {
      return null
    }

    return (
      <tr>
        <td></td>
        <td>{rating}</td>
      </tr>
    )
  }

  team () {
    const { team } = this.props
    if (!team) {
      return null
    }

    const { translators, editors } = team
    return (
      <tr>
        <td>`</td>
        <td>
          {translators
            ? (
              <div className='icon-container'>
                <CommentOutlined title='' className='icon' />
                <span className='translators'> {translators.sort().join(', ')} </span>
              </div>
              )
            : null}
          {editors
            ? (
              <div className='icon-container'>
                <CheckOutlined title='' className='icon' />
                <span className='editors'> {editors.sort().join(', ')} </span>
              </div>
              )
            : null}
        </td>
      </tr>
    )
  }

  description () {
    const { description } = this.props
    if (!description) {
      return null
    }

    return (
      <>
        <p> <strong> </strong>
          {description}
        </p>
      </>
    )
  }

  notes () {
    const { notes } = this.props
    if (!notes) {
      return null
    }

    return (
      <>
        <p> <i>. </i>
          {notes}
        </p>
      </>
    )
  }

  tags () {
    const { meta } = this.props
    if (!meta) {
      return null
    }

    const { tags } = meta
    if (!tags) {
      return null
    }

    console.log('tags:', tags)

    const tagList = tags.sort().map(tag => <span className='tag' key={tag}> {tag} </span>)

    return (
      <p>
        {tagList}
      </p>
    )
  }

  render () {
    const { path, title, complete } = this.props

    return (
      <div className='information-card'>
        <Link to={path}> {title} </Link> <CompletedIndication complete={complete} />
        <div className='content'>
          <div className='img-container'>

            <img src={`https://raw.githubusercontent.com/high-otaku/assets/master/${path}.png`} alt='' />

          </div>

          <table>
            <tbody>

              {this.authors()}
              {this.genres()}
              {this.foreignLanguages()}
              {this.rating()}
              {this.team()}

              <tr />
            </tbody>

          </table>

          {this.description()}
        </div>

        <div className='secondary-content'>
          {this.notes()}
          {this.tags()}
        </div>
      </div>
    )
  }
}

export default Card
